import ExtensionsHeader from "@components/extensions-header";
import { useEffect, useState } from "react";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import AppCollapse from "@components/app-collapse";

import Repeater from "@components/repeater";
import { X, Plus } from "react-feather";
import Faq from "../../../../../pages/faq";

import {
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Form,
  FormGroup,
  Label,
  Input,
  Button,
} from "reactstrap";
import api from "../../../../../../constants/api";
import request from "../../../../../../services/request";
import Sidebar from "./add/index";
import Faqs from "../../../../../pages/faq/Faqs";

const CustomHeader = ({ toggleSidebar, data }) => {
  const history = useHistory();
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 ">
      {
        <Row className="justify-content-end">
          <Col
            xl="6"
            className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
          >
            <Button.Ripple
              color="primary"
              onClick={() => history.push("/apps/cms/setting/faq/edit")}
            >
              <FormattedMessage id="update_tab" />
            </Button.Ripple>
          </Col>
        </Row>
      }
    </div>
  );
};

const CityList = () => {
  const lang = useSelector((state) => state.common.language);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [disable, setDisable] = useState(false);
  const [faqData, setfaqData] = useState();
  const [count, setCount] = useState([]);
  useEffect(() => {
    (async () => {
      const listAbout = await request.send({
        method: api.LIST_ABOUT.method,
        path: api.LIST_ABOUT.path,
        query: { lang },
      });
      if (listAbout?.data?.data?.length > 0) {
        setfaqData(JSON.parse(listAbout?.data?.data[0].faq));
      }
    })();
  }, []);
  const toggleSidebar = () => {
    setDisable(false);
    setSidebarOpen(!sidebarOpen);
  };

  return (
    <div className="app-user-list">
      <CustomHeader toggleSidebar={toggleSidebar} />
      <ExtensionsHeader title={<FormattedMessage id="FAQ" />} />
      <>
        <Card>
          <CardBody>
            {faqData && (
              <AppCollapse
                className="mt-2"
                type="margin"
                data={faqData}
                titleKey="question"
                contentKey="ans"
                accordion
                //  {...(item.title === 'Payment' ? { active: 1 } : {})}
              />
            )}
          </CardBody>
        </Card>
      </>
      <Sidebar
        open={sidebarOpen}
        toggleSidebar={toggleSidebar}
        disabled={disable}
        {...{ disable, setDisable }}
      />
    </div>
  );
};

export default CityList;
