import ExtensionsHeader from "@components/extensions-header";
import { useEffect, useState } from "react";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import { CardBody } from "reactstrap";

import { Button, Card, Col, Row } from "reactstrap";
import api from "../../../../../../constants/api";
import request from "../../../../../../services/request";
import Sidebar from "./add/index";
import { getData } from "./store1/action";
import { toast } from "react-toastify";

const CustomHeader = ({}) => {
  const history = useHistory();
  const lang = useSelector((state) => state.common.language);
  const handerupdate = () => {
    if (lang == "vn") {
      history.push("/apps/cms/setting/company_information/edit/1");
    } else if (lang == "en") {
      history.push("/apps/cms/setting/company_information/edit/2");
    } else {
      history.push("/apps/cms/setting/company_information/edit/3");
    }
  };
  console.log(lang);
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 ">
      {
        <Row className="justify-content-end">
          <Col
            xl="6"
            className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
          >
            <Button.Ripple
              color="primary"
              onClick={() => handerupdate()}
              // onClick={() => history.push("/apps/cms/setting/company_information/edit/1")}
            >
              <FormattedMessage id="update_tab" />
            </Button.Ripple>
          </Col>
        </Row>
      }
    </div>
  );
};

const CompanyInfomationList = () => {
  // const lang = useSelector((state) => state.common.language);
  // const [sidebarOpen, setSidebarOpen] = useState(false);
  // const [disable, setDisable] = useState(false);
  // const dispatch = useDispatch();
  // const store = useSelector((state) => state.company_information);
  // const [companyData, setcompanyData] = useState();
  // useEffect(() => {
  //   (async () => {
  //     const listAbout = await request.send({
  //       method: api.LIST_COMPANY_INFOMATION.method,
  //       path: api.LIST_COMPANY_INFOMATION.path,
  //       query: { lang },
  //     });
  //     if (listAbout?.data?.data?.length > 0) {
  //       setcompanyData(listAbout?.data?.data);
  //     }
  //     console.log(listAbout);
  //   })();
  // }, []);
  // const toggleSidebar = () => {
  //   setDisable(false);
  //   setSidebarOpen(!sidebarOpen);
  // };

  const dispatch = useDispatch();
  const store = useSelector((state) => state.company_information);
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [disable, setDisable] = useState(false);
  const lang = useSelector((state) => state.common.language);

  const [loadData, setLoadData] = useState(false);
  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
    setDisable(false);
  };

  useEffect(() => {
    dispatch(
      getData({
        filter: {
          lang,
        },
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, []);

  useEffect(() => {
    switch (store?.type) {
      case "ADD_COMPANY":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"successfully added new!"} />);
          setLoadData(!loadData);
          toggleSidebar();
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Add new failure!"} />);
        }
        break;
      case "UPDATE_COMPANY":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"Update successful!"} />);
          setLoadData(!loadData);
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Update failed!"} />);
        }
        break;
      default:
        break;
    }
  }, [store.status]);

  return (
    <div className="app-user-list">
      <CustomHeader toggleSidebar={toggleSidebar} />
      <ExtensionsHeader title={<FormattedMessage id="section_system" />} />
      <Row>
        <Col md={6}>
          <Card className="card-apply-job " style={{ height: "unset" }}>
            <CardBody>
              <div
                dangerouslySetInnerHTML={{
                  __html: store?.data[0]?.content + "",
                }}
              ></div>
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Sidebar
        open={sidebarOpen}
        toggleSidebar={toggleSidebar}
        disabled={disable}
        {...{ disable, setDisable }}
      />
    </div>
  );
};

export default CompanyInfomationList;
